body {
  font-family: "Source Sans Pro", sans-serif;
}

.MainContainer {
  padding: 4% 12%;
  margin-top: 32px;

  box-sizing: border-box;
  width: 100%;
  display: flex;
  align-items: center;
}

.ProductPreview {
  width: 50%;
  position: relative;
}

.ProductPreview > img {
  width: 100%;
}

.TimeSection {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-size: 48px;
  font-weight: 600;
}

.HeartBeatSection {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-size: 48px;
  font-weight: 600;
}
.HeartBeatSection > i {
  color: red;
  animation: beat 1s infinite;
}

.HeartBeatSection > p {
  margin-top: 16px;
}

@keyframes beat {
  0% {
    transform: scale(1, 1);
  }
  50% {
    transform: scale(1.2, 1.2);
  }

  100% {
    transform: scale(1, 1);
  }
}

.ProductData {
  width: 50%;
}
.ProductTittle {
  font-size: 48px;
  font-weight: 600;
  margin-top: 0;
  margin-bottom: 16px;
}
.ProductDescription {
  font-size: 20px;
  color: rgb(0, 0, 0, 0.8);
  margin-top: 0;
  margin-bottom: 16px;
}

.ProductImage {
  width: 100%;
  max-width: 80px;

  border: 2px solid transparent;
  border-radius: 8px;
  margin-right: 12px;
  cursor: pointer;
}

.SelectedProductImage {
  border: 2px solid teal;
}

.SectionHeading {
  font-size: 28px;
  margin-bottom: 8px;
}

.FeatureItem {
  min-width: 80px;
  background-color: gainsboro;
  border-radius: 8px;
  border: 0;
  padding: 12px 24px;
  text-align: center;
  margin-right: 12px;
  box-sizing: content-box;
  cursor: pointer;
  font-weight: 600;
}

.SelectedFeatureItem {
  color: white;
  background-color: gray;
}
.PrimaryButton {
  color: white;
  background-color: teal;
  font-size: 20px;
  border-radius: 8px;
  border: 0;
  padding: 12px 24px;
  margin-top: 26px;
  cursor: pointer;
}
